
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Checklist from 'vux/src/components/checklist/index.vue'

import { mapState, mapActions } from 'vuex'

export default {
directives: {
TransferDom
},
components: {
Swiper,
SwiperItem,
Group,
XInput,
XButton,
XSwitch,
PopupHeader,
Popup,
Radio,
Cell,
Flexbox,
FlexboxItem,
Checklist,
},
computed: {
...mapState({
curIndex: state => state.curIndex,
})
},
methods: {
changePlan(index){
this.$store.commit("updateIndex", { curIndex: +index })
},
listChange(val) {
this.branchListValue = val
},
fetchBrancchName(branchId){
var arrayFilter = this.branchList.filter(function(item) {
return item.key == branchId;
});
return arrayFilter[0].value
}
},
watch: {
curIndex(curVal, oldVal){
console.log(curVal);
}
},
data () {
return {
show1: false,
branchList: [
{
key: '1',
value: '001'
},
{
key: '2',
value: '002'
},
{
key: '3',
value: '003'
},
{
key: '4',
value: '004'
},
{
key: '5',
value: '005'
},
{
key: '6',
value: '006'
},
{
key: '7',
value: '007'
},
{
key: '8',
value: '008'
},
{
key: '9',
value: '009'
},
{
key: '10',
value: '010'
},
{
key: '11',
value: '011'
}
],
branchListValue: ['1', '2'],
}
}
}



